@import "../../../styles/vars";
@import "../../../styles/mixins";

.appDevelopmentDesktopApps {
  @include flexCol();
  gap: 172px;
  position: relative;
  z-index: 1;
  margin-bottom: 178px;

  @include xxlScreenMedia {
    gap: 120px;
    margin-bottom: 148px;
  }
  @include xlScreenMedia {
    gap: 70px;
    margin-bottom: 118px;
  }

  @include lgScreenMedia {
    flex-direction: column;
    gap: 40px;
    margin-bottom: 58px;
  }

  @include smScreenMedia {
    gap: 20px;
    margin-bottom: 20px;
  }

  &__imgBlock {
    width: 100%;
    background: #232421;
    padding: 7px 27px;
    overflow: hidden;
    border-radius: 40px;

    @include mdScreenMedia {
      padding: 5px 17px;
      border-radius: 20px;
    }

    @include smScreenMedia {
      padding: 5px 17px;
    }

    @media (max-width: 460px) {
      border-radius: 10px;
      padding: 3px 10px;
    }
  }

  &__imgMainBlock {
    @include flexCol(flex-start,flex-start);
  }

  &__headerImg {
    width: 100%;
    margin-bottom: 11px;
  }

  &__mainImg {
    width: 78.3%;
  }

  &__aside {
    width: 22%;
  }

  &__singerImg {
    width: 100%;
  }

  &__player {
    margin-top: 5px;
    margin-bottom: 22px;

    @include xxlScreenMedia {
      margin-bottom: 12px;
    }

    @include xlScreenMedia {
      margin-bottom: 6px;
    }

    @include lgScreenMedia {
      margin-bottom: 22px;
    }
    @include mdScreenMedia {
      margin-bottom: 12px;
    }

    @include smScreenMedia {
      margin-bottom: 6px;
    }

    @media (max-width: 460px) {
      margin-bottom: 3px;
      margin-top: 3px;
    }
  }

  &__playBarBlock {
    @include flexCol(space-between,flex-end);
    gap: 11px;
    margin-bottom: 17px;
    width: 100%;

    @include xxlScreenMedia {
      gap: 6px;
      margin-bottom: 10px;
    }

    @include xlScreenMedia {
      margin-bottom: 6px;
    }

    @include lgScreenMedia {
      gap: 11px;
      margin-bottom: 17px;
    }

    @include mdScreenMedia {
      gap: 6px;
      margin-bottom: 10px;
    }

    @include smScreenMedia {
      margin-bottom: 6px;
    }
  }

  &__playerTime {
    @include setFont(13px, 300);
    color: $color-white;

    @include xxlScreenMedia {
      font-size: 10px;
    }

    @include xlScreenMedia {
      font-size: 8px;
    }

    @include lgScreenMedia {
      font-size: 13px;
    }

    @include mdScreenMedia {
      font-size: 10px;
    }

    @include smScreenMedia {
      font-size: 8px;
    }

    @media (max-width: 460px) {
      font-size: 5px;
    }
  }

  &__playBar {
    position: relative;
    width: 100%;
    display: flex;
  }

  &__playBarIcon,
  &__playBarIconInner {
    width: 100%;

  }

  &__playBarIcon {
    fill: $color-white;
  }

  &__playBarInner {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    display: flex;
  }

  &__playBarIconInner {
    fill: #68FF8C;
  }

  &__playerControls {
    @include flexCol(space-around);
  }

  &__playerBtn {
    background: transparent;
    border: none;
    cursor: pointer;
    display: flex;

    &:hover {
      svg {
        fill: #68FF8C;
      }
    }
  }

  &__playerControlIcon {
    @include svg(19px, $color-white);

    @include xxlScreenMedia {
      width: 14px;
      height: 14px;
    }

    @include xlScreenMedia {
      width: 10px;
      height: 10px;
    }

    @include lgScreenMedia {
      width: 19px;
      height: 19px;
    }

    @include mdScreenMedia {
      width: 14px;
      height: 14px;
    }

    @include smScreenMedia {
      width: 10px;
      height: 10px;
    }

    @media (max-width: 460px){
      width: 5px;
      height: 5px;
    }
  }

  &__sideImg {
    width: 100%;
  }

  &__textBlock {
    width: 100%;
    max-width: 531px;

    @include lgScreenMedia {
      margin-bottom: 26px;
      text-align: center;
    }
  }

  &__title {
    margin-bottom: 40px;

    @include xxlScreenMedia {
      margin-bottom: 30px;
    }

    @include lgScreenMedia {
      margin-bottom: 26px;
    }

    @include smScreenMedia {
      margin-bottom: 20px;
    }
  }

  &__contentText {
    &:not(:last-child) {
      margin-bottom: 30px;

      @include smScreenMedia {
        margin-bottom: 20px;
      }
    }
  }

}
